/* Variables de colores */
@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Poppins&display=swap');

:root {
  --background-light: #5FE1D9;
  --text-light: #000;
  --border-light: green;

  --text-dark: #fff;
  --border-dark: #fff;
  --background-dark: red;

  --link-color: #000;
  --link-hover-color: #ccc;
}

/* Estilos generales del cuerpo */
body {
  background-color: #5FE1D9;
  color: var(--text-light);
  border-color: var(--border-light);
  transition: background-color 0.3s, color 0.3s;
  font-family: 'Poppins', sans-serif;
  font-size: 16px;
}

/* Modo oscuro */
@media (prefers-color-scheme: dark) {
  body {
    background-color: var(--background-dark);
    color: var(--text-dark);
    border-color: var(--border-dark);
  }

  /* Estilos específicos en modo oscuro */
  .footer-img {
    width: 5em;
  }

  .contact-content a {
    color: var(--text-dark);
    text-decoration: none;
  }
}

/* Estilos en modo claro (fuera del modo oscuro) */
.contact-content a {
  color: var(--text-light);
  text-decoration: none;
}

/* Encabezado */
.header {
  padding: 20px;
  display: flex;
  justify-content: end;
  border-bottom: 3px solid var(--border-light);
}

.header-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

/* Barra de navegación */
.navbar {
  text-decoration: none;
  color: var(--link-color);
}

/* Barra de navegación */
.navbar a {
  padding: 10px;
  text-decoration: none;
  color: var(--link-color);
}

.nav-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.nav-item {
  margin: 5px;
}

/* Sección 'Acerca de' */
.about-container {
  max-width: 100%;
  margin: 0 auto;
  border-bottom: 3px dotted var(--border-light);
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.about-title {
  font-size: 3em;
}

.about-content {
  font-size: 4em;
  font-family: 'Bebas Neue', sans-serif;
}

/* Imagen del avatar */
.avatar img {
  padding: 1em;
  margin: 1em;
  width: 100%;
  max-width: 10em;
  border-radius: 50%;
  box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);
}

/* Sección de contacto */
.contact-container {
  max-width: 100%;
  margin: 0 auto;
  padding: 20px;
  font-size: 2em;
  text-align: center;
  border-bottom: 3px solid var(--border-light);
}

/* Sección de habilidades */
.skills-container {
  max-width: 100%;
  margin: 0 auto;
  padding: 20px;
  text-align: center;
  border-bottom: 3px solid var(--border-light);
}

.skills-title a,
.skills-title {
  color: var(--text-dark);
  padding: 10px;
  text-decoration: none;
}

.skills-list {
  list-style-type: none;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 1em;
}

/* Sección de proyectos */
.projects-container {
  max-width: 100%;
  margin: 0 auto;
  padding: 20px;
  border-bottom: 3px solid var(--border-light);
}

.projects-container img {
  max-width: 100%;
  margin: 0 auto;
  padding: 20px;
}

/* Pie de página */
.footer-container {
  max-width: 100%;
  margin: 0 auto;
  padding: 20px;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.footer-text {
  font: 1.5em sans-serif;
}

.footer-img {
  width: 1em;
}

/* Botón de alternar */
.toggle-btn {
  position: fixed;
  top: 10px;
  left: 10px;
  background-color: transparent;
  padding: 5px 10px;
  cursor: pointer;
  border: 0px solid var(--border-light);
}

.projects-list {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
  align-items: center;
}

.project-item {
  text-align: center;
}

.project-image {
  width: 100%;
  max-height: 200px;
  object-fit: cover;
}

/* Media queries for responsiveness */
@media only screen and (max-width: 768px) {
  /* Adjust styles for screens up to 768px width */
  .header {
    justify-content: center;
  }

  .header-content {
    align-items: center;
  }

  .navbar {
    text-align: center;
  }

  .nav-list {
    justify-content: center;
  }

  .about-container {
    text-align: center;
  }

  .about-title {
    font-size: 2em;
  }

  .about-content {
    font-size: 3em;
  }

  .avatar img {
    max-width: 8em;
  }

  .contact-container {
    font-size: 1.5em;
  }

  .skills-container {
    font-size: 1.5em;
  }

  .projects-container {
    font-size: 1.5em;
  }

  .footer-container {
    font-size: 1.2em;
  }

  .footer-img {
    width: 0.8em;
  }

  .toggle-btn {
    top: 5px;
    left: 5px;
  }

  .projects-container img {
    max-height: 150px;
  }

  .project-image {
    max-height: 150px;
  }
}

